<template>
  <div>
    <section class="container section">
      <div class="columns">
        <div class="column">
          <h1
            v-if="person.firstName"
            class="title has-text-poppins has-weight-light is-1 has-red-stripe"
          >
            Hi {{ personName }}
          </h1>
          <h1
            v-else
            class="title has-text-poppins has-weight-light is-1 has-red-stripe"
          >
            Welcome to the community!
          </h1>
        </div>
      </div>

      <div class="columns">
        <div class="column is-7">
          <div class="content">
            <p>
              The TEDxAuckland community is made up of a diverse array of change
              makers, creators and those who like new ideas.
            </p>
            <p>
              That much we know is true, but we'd love to know more about you.
            </p>
            <p>
              Wed be so grateful if you can take a minute to confirm some of
              your details and share a bit more about yourself.
            </p>
            <p>
              Everyone who completes the survey will have the chance to win a
              <strong>two year VIP pass to all TEDxAuckland events</strong> and
              <strong>two years subscription to idealog</strong>!
            </p>
          </div>
        </div>
        <div class="column is-5">
          <img
            src="/assets/ticketing-app/images/photo-of-crowd.jpg"
            alt="Photograph of the TEDxAuckland Community"
          />
        </div>
      </div>
    </section>

    <section class="container section">
      <form @submit.prevent>
        <h2 class="title has-text-poppins is-5 has-red-stripe">
          let's first confirm a few details about you
        </h2>
        <div class="columns">
          <div class="column is-6">
            <b-field
              label="First Name"
              :type="{ 'is-danger': $v.personAnswers.firstName.$error }"
              :message="
                $v.personAnswers.firstName.$error
                  ? 'Please enter your first name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.firstName"
                placeholder="Leslie"
                @blur="$v.personAnswers.firstName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Last Name"
              :type="{ 'is-danger': $v.personAnswers.lastName.$error }"
              :message="
                $v.personAnswers.lastName.$error
                  ? 'Please enter your last name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.lastName"
                placeholder="Miles"
                @blur="$v.personAnswers.lastName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Gender"
              :type="{ 'is-danger': $v.personAnswers.data.gender.$error }"
            >
              <b-input
                v-model.number="personAnswers.data.gender"
                type="text"
                @blur="$v.personAnswers.data.gender.$touch()"
              >
              </b-input>
            </b-field>
          </div>
          <div class="column is-6">
            <b-field
              label="What year were you born?"
              :type="{ 'is-danger': $v.personAnswers.data.yearOfBirth.$error }"
              :message="[
                $v.personAnswers.data.yearOfBirth.$error &&
                !$v.personAnswers.data.yearOfBirth.minLength
                  ? 'Please use four digits, eg 1994'
                  : '',
                $v.personAnswers.data.yearOfBirth.$error &&
                !$v.personAnswers.data.yearOfBirth.required
                  ? 'Please fill in your birth year'
                  : ''
              ]"
            >
              <b-input
                v-model.number="personAnswers.data.yearOfBirth"
                type="number"
                placeholder="1987"
                @blur="$v.personAnswers.data.yearOfBirth.$touch()"
              >
              </b-input>
            </b-field>

            <b-field
              label="Which ethnicity do you identify as?"
              :type="{ 'is-danger': $v.personAnswers.data.ethnicity.$error }"
            >
              <b-input
                v-model.number="personAnswers.data.ethnicity"
                type="text"
                @blur="$v.personAnswers.data.ethnicity.$touch()"
              >
              </b-input>
            </b-field>

            <b-field
              label="Email"
              :type="{
                'is-danger': $v.personAnswers.email.$error || errors.email
              }"
              :message="errors.email ? errors.email : ''"
            >
              <b-input
                v-model="personAnswers.email"
                placeholder="francis@example.com"
                @blur="$v.personAnswers.email.$touch()"
                type="email"
                icon="email"
              >
              </b-input>
            </b-field>

            <b-field
              label="Please enter your address"
              :type="{ 'is-danger': $v.personAnswers.data.address.$error }"
              :message="
                $v.personAnswers.data.address.$error
                  ? 'Please enter your address and pick from the list'
                  : ''
              "
            >
              <vue-google-autocomplete
                id="map"
                classname="input"
                :class="{ 'is-danger': $v.personAnswers.data.address.$error }"
                :placeholder="addressPlaceholder"
                :enable-geolocation="true"
                @placechanged="getAddressData"
                @blur="$v.personAnswers.data.address.$touch()"
              >
              </vue-google-autocomplete>
            </b-field>

            <div class="">
              {{ formattedAddress }}
            </div>
          </div>
        </div>

        <email-exists-modal
          @closeModal="emailExistsModalActive = false"
          :active="emailExistsModalActive"
          :email="personAnswers.email ? personAnswers.email : ''"
        />

        <div class="columns is-mobile">
          <div class="column">
            <survey-nav :id="$route.params.id" :active="1" />
          </div>

          <div class="column">
            <div class="buttons has-addons is-right">
              <div
                class="button is-primary is-medium"
                @click.prevent="nextPage"
              >
                Next
              </div>
            </div>
          </div>
        </div>
      </form>
    </section>
  </div>
</template>

<script>
import NProgress from "nprogress";
import { required, integer, email, minLength } from "vuelidate/lib/validators";
import { personNameMixin } from "../mixins/personNameMixin";
import SurveyNav from "@/community/components/SurveyNav";
import EmailExistsModal from "@/community/components/EmailExistsModal";

import VueGoogleAutocomplete from "vue-google-autocomplete";

import ProfileService from "@/services/ProfileService";
export default {
  name: "SurveyPageOne",
  components: { SurveyNav, EmailExistsModal, VueGoogleAutocomplete },
  data() {
    return {
      personAnswers: this.person,
      emailExistsModalActive: false,
      errors: {}
    };
  },
  props: {
    person: {
      type: Object,
      required: true
    }
  },
  mixins: [personNameMixin],
  validations: {
    personAnswers: {
      firstName: { required },
      lastName: { required },
      email: { required, email },
      data: {
        address: { required },
        gender: { required },
        ethnicity: { required },
        yearOfBirth: {
          required,
          minLength: minLength(4)
        }
      }
    }
  },
  computed: {
    formattedAddress() {
      if (
        Object.keys(this.personAnswers.data.address).length > 0 &&
        this.personAnswers.data.address.constructor === Object
      ) {
        const address = this.personAnswers.data.address;
        return `${address.streetNumber} ${address.route}, ${
          address.locality
        }, ${address.administrativeAreaLevel1}, ${address.postalCode}`;
      } else {
        null;
      }
    },
    addressPlaceholder() {
      if (this.formattedAddress) {
        return this.formattedAddress;
      } else {
        return "123 road suburb city country";
      }
    },
    emailForWatch() {
      return this.personAnswers.email;
    }
  },
  watch: {
    emailForWatch(val) {
      if (this.errors.email) {
        this.errors.email = null;
      }
    }
  },
  methods: {
    nextPage() {
      this.$v.$touch();
      if (!this.$v.$invalid && !this.errors.email) {
        NProgress.start();
        if (this.person.id === null) {
          ProfileService.createProfile({
            person: {
              firstName: this.personAnswers.firstName,
              lastName: this.personAnswers.lastName,
              email: this.personAnswers.email,
              source: this.personAnswers.source
            },
            profile: this.person.data
          })
            .then(response => {
              this.$router.push({
                name: "survey-two",
                params: { id: response.data.person.id }
              });
              NProgress.done();
            })
            .catch(e => {
              NProgress.done();
              console.log("ERROR", e);
              this.errors = e.response.data.error;
              this.emailExistsModalActive = true;
            });
        } else {
          ProfileService.updateProfile(this.person.id, {
            person: {
              firstName: this.personAnswers.firstName,
              lastName: this.personAnswers.lastName,
              email: this.personAnswers.email
            },
            profile: this.person.data
          })
            .then(() => {
              this.$router.push({
                name: "survey-two",
                params: { id: this.person.id }
              });
              NProgress.done();
            })
            .catch(e => {
              NProgress.done();
              console.log("ERROR", e);
            });
        }
      }
    },
    getAddressData(data) {
      this.personAnswers.data.address = {
        streetNumber: data.street_number,
        route: data.route,
        locality: data.locality,
        administrativeAreaLevel1: data.administrative_area_level_1,
        country: data.country,
        postalCode: data.postal_code,
        latitude: data.latitude,
        longitude: data.longitude
      };
    }
  }
};
</script>

<style></style>
